import axios from 'axios'
import { ElMessage } from 'element-plus'
import router from '../router/index'
import { localGet } from './index'


// ËøôËæπÁî±‰∫éÂêéÁ´ØÊ≤°ÊúâÂå∫ÂàÜÊµãËØïÂíåÊ≠£ÂºèÔºåÂßë‰∏îÈÉΩÂÜôÊàê‰∏Ä‰∏™Êé•Âè£„ÄÇ
axios.defaults.baseURL = "https://www.fastmock.site/mock/d499747d0e63b8a6c416120a3375b44c/test1"
// Êê∫Â∏¶ cookieÔºåÂØπÁõÆÂâçÁöÑÈ°πÁõÆÊ≤°Êúâ‰ªÄ‰πà‰ΩúÁî®ÔºåÂõ†‰∏∫Êàë‰ª¨ÊòØ token Èâ¥ÊùÉ
axios.defaults.withCredentials = true
// ËØ∑Ê±ÇÂ§¥Ôºåheaders ‰ø°ÊÅØ
axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'
axios.defaults.headers['token'] = localGet('token') || ''
// ÈªòËÆ§ post ËØ∑Ê±ÇÔºå‰ΩøÁî® application/json ÂΩ¢Âºè
axios.defaults.headers.post['Content-Type'] = 'application/json'

// ËØ∑Ê±ÇÊã¶Êà™Âô®ÔºåÂÜÖÈÉ®Ê†πÊçÆËøîÂõûÂÄºÔºåÈáçÊñ∞ÁªÑË£ÖÔºåÁªü‰∏ÄÁÆ°ÁêÜ„ÄÇ
axios.interceptors.response.use(res => {
    if (typeof res.data !== 'object') {
      ElMessage.error('ÊúçÂä°Á´ØÂºÇÂ∏∏ÔºÅ')
      return Promise.reject(res)
    }
    if (res.data.data.resultCode != 666) {
      if (res.data.data.failedMessage) ElMessage.error(res.data.data.failedMessage)
      if (res.data.data.resultCode == 555) {
        router.push({ path: '/login' });
      }
      return Promise.reject(res.data)
    }
    //Ê≥®ÊÑèÂú®mock‰∏äÂÜôË¶Å‰º†ÁöÑÊï∞ÊçÆÊó∂Ë¶ÅÊääÂÆÉÊîæÂú®data.userInfo‰∏ä
  return res.data.data.userInfo
})

export default axios